@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;700&display=swap);
.App {
  text-align: center;
  min-height: 100vh;
  width: 100%;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.app-header {
  padding: 30px 30px;
}

.generator-button > span {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 5px;
}

.generator-button-icon {
  margin-right: 10px;
}

.activity {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 80%;
  min-height: 320px;
  margin: 0 auto;
  border: 1px solid white;
  border-radius: 20px;
  padding: 20px;
}

.guide-icon {
  cursor: pointer;
  -webkit-animation: grow 4s ease forwards infinite;
          animation: grow 4s ease forwards infinite;
  position: absolute;
  top: 20px;
  right: 20px;
  border-radius: 50%;
  z-index: 2;
}

.activity-content {
  margin: auto 0;
}

.activity-heading {
  margin-bottom: 40px;
}

.activity-heading > p {
  font-size: 12px;
  font-weight: 200;
}

.activity-misc {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto;
}

.activity-misc > div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.activity-icon {
  margin-right: 10px;
}

.bg-light {
  background: white;
}

.close-icon {
  color: black;
}

@media screen and (min-width: 992px) {
  .activity {
    width: 40%;
  }
}

.app-footer {
  width: 100%;
  padding: 20px;
  bottom: 0;
  margin-top: 40px;
}

.footer-text {
  font-size: 12px;
  font-weight: 300;
}

.footer-buttons {
  margin: 0 auto 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 50%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.social-media-button {
  cursor: pointer;
}

.social-media-button > a {
  text-decoration: none;
  color: white;
}

@media screen and (min-width: 992px) {
  .footer-buttons {
    width: 10%;
  }
}
.guide {
  width: 100%;
  min-height: 320px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.guide-content {
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 20px;
  background: white;
  color: black;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 300px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.guide-inner {
  padding: 20px;

  height: 100%;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.guide-inner > ul {
  list-style-type: none;
  text-align: left;
}

.guide-inner > h2,
li {
  margin-bottom: 8px;
}

.guide-slogan {
  color: rgba(0, 0, 0, 0.5);
  font-size: 12px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@-webkit-keyframes grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.generator-button {
  margin: 50px auto;
  padding: 10px;
  border: 1px solid white;
  width: 200px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  transition: 300ms ease-in-out all;
  cursor: pointer;
  border-radius: 10px;
}

.generator-button:hover {
  background: white;
  color: black;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Nunito", "Roboto", "Ubuntu", sans-serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: no-repeat
    linear-gradient(-45deg, #db613c, #cc2e6a, #19bbf7, #2ab897);
  background-size: 400% 400%;
  -webkit-animation: changeBackground 20s ease infinite;
          animation: changeBackground 20s ease infinite;
  height: 100vh;
  width: 100vw;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes changeBackground {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes changeBackground {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

